<template>
    <div id="base-set">
        <h3 class="title"></h3>

        <div class="content">

            <!--  -->
            <div class="food-sn clearfix">
                <div class="sn-title food-left left">
                    <span></span>
                </div>
                <div class="sn-content food-right left">
                    <span>{{ foodinfo.food_id }}</span>
                </div>
            </div>

            <!--  -->
            <div class="food-code clearfix">
                <div class="code-title food-left left">
                    <span></span>
                </div>
                <div class="code-content food-right left">
                    <!-- <img src="../images/empty.png" alt=""> -->
                    <img :src="base_url + '/img_get.php?get_food_qrcode=1&shop_id=' + foodinfo.shop_id + '&food_id=' + foodinfo.food_id">
                </div>
            </div>

            <!-- name -->
            <div class="food-name clearfix">
                <div class="name-title food-left left">
                    <span class="must"></span>
                </div>
                <div class="name-content food-right left">
                    <el-input 
                    v-model="foodNameN" 
                    placeholder=""
                    @change="foodNameHandler"></el-input>
                </div>
            </div>

            <!-- category -->
            <div class="food-category clearfix">
                <div class="category-title food-left left">
                  <span class="must"></span>
                </div>
                <div class="category-content food-right left">
                    <select-cate
                    v-if="isShowCate"
                    :categoryArr="categoryArr"
                    :getcategoryType="getcategoryType"
                    @on-changeT="clearcategoryId"
                    @on-change="getSelectId"
                    @on-opencate="opencategoryDialog"></select-cate>
                </div>
            </div>

            <!--   @click="changeUnit(index)"-->
            <div class="food-unit clearfix">
                <div class="unit-title food-left left"><span class="must">
                  </span></div>
                <ul class="unit-content food-right left clearfix">
                    <li class="left" 
                    :class="{active: unitSelect === index}" 
                    v-for="(item,index) in unitData" 
                    :key="index" 
                    >{{ item }}</li>
                    <li class="editor left" @click="showUnitDialog"></li>
                </ul>
            </div>

            <!--  -->
            <div class="food-feature clearfix">
                <div class="feature-title food-left left"></div>
                <ul class="feature-content food-right left clearfix">
                    <li class="left" v-for="(item, index) in featureData" :key="item.index">{{ item }}</li>
                    <li class="editor left" @click="showFeatureDialog"></li>
                </ul>
            </div>
            <div class="feature-tip">105</div>

            <!--  -->
            <div class="food-size clearfix">
                <div class="size-title food-left left"><span class="must">
                  </span></div>
                <ul class="size-content food-right left">
                    <li class="left" 
                    :class="{active: sizeSelect === item.value}" 
                    v-for="(item, index) in sizeData" 
                    :key="item.value" 
                    @click="changeSizeSelect(item.value)">{{ item.label }}</li>
                </ul>
            </div>

            <!--  -->
            <div class="food-price clearfix">
                <div class="price-title food-left left"><span class="must">
                  </span></div>
                <div class="price-content food-right left">
                    <div class="tip">
                        1.2.3.4.
                    </div>
                    <size-price 
                    :foodPrice="foodinfo.food_price" 
                    :typeSelect="sizeSelect"
                    @price-change="getPrice"></size-price>
                </div>
            </div>

            <!--  -->
            <div class="food-picture clearfix">
                <div class="picture-title food-left left"></div>
                <div class="picture-content food-right left">
                      <div class="default-img" v-if="imgList.length === 0">
                        <img src="../images/empty.png" alt="">
                      </div>
                      <ul class="picture-group clearfix left" >
                        <li v-for="(item, index) in imgList" 
                        :key="item" 
                        class="left"                       
                        >
                          <img :src="base_url + '/img_get.php?img=1&height=100&width=100&imgname='+ item">

                            <span class="picture-actions">
                              <span class="picture-preview">
                                <i class="el-icon-view" @click="isshowPicture(index)"></i>
                              </span>
                              <span class="picture-delete">
                                <i class="el-icon-delete2" @click="deleteimg(index)"></i>
                              </span>
                            </span>
                        </li>
                      </ul>
                      <div class="sl-imageup left">
                        <s-upload @send-image="imgChangeHandler"></s-upload>
                        <div class="img-tip">JPGJPEGPNG500KB</div>
                      </div>
                </div>
            </div>
            
            <!--  -->
            <div class="food-composition clearfix">
                <div class="composition-title food-left left"></div>
                <ul class="composition-content food-right left clearfix">
                    <li class="left" v-for="(item, index) in compositionData" :key="item.index">{{ item }}</li>
                    <li class="editor left" @click="showCompositionDialog"></li>
                </ul>
            </div>
            <div class="feature-tip">105</div>

            <!--  -->
            <!-- <div class="food-stock clearfix">
                <div class="stock-title food-left left">
                    <span></span>
                </div>
                <div class="stock-content food-right left">
                    <el-input  @keyup.native = "checkStockNum"
                    v-model="stockNum" 
                    placeholder="0"
                    @change="changeStock"></el-input>
                </div>
            </div> -->
            <div class="food-stock">
              <stock-select :stockNumber="stockNum"
              @change-stocknum="getStockNum"></stock-select>
            </div>
        </div>

        <!--  -->
        <tag-editor 
        :dialogVisible="dialogVisible"
        :selectArr="featureData"
        :tagObj="featureObj"
        @on-close="hideDialog"
        @on-change="getFeature"
        ></tag-editor>

        <!--  -->
        <tag-editor 
        :dialogVisible="compositionDialog"
        :selectArr="compositionData"
        :tagObj="compositionObj"
        @on-close="hideCompositionDialog"
        @on-change="getComposition"
        ></tag-editor>

        <!--  -->
        <tag-editor 
        :dialogVisible="unitdialogVisible"
        :selectArr="unitData"
        :tagObj="unitObj"
        @on-close="hideUnitDialog"
        @on-change="getUnit"
        ></tag-editor>

        <!--  -->
        <show-img v-if="showPicture" @close-img="isclosePicture" :imgSrc="imgSrc"></show-img>
        <!--  -->

        <!--  -->
        <div class="category-dialog-m" v-if="categoryDialogVisible">
          <div class="dialog-wrap">
            <div class="dialog-cover" @click="dialogAddCancelFirst"></div>
            <div class="dialog-content">
              <div class="dialog-title"></div>
              <div class="category-content">
                <editor-tree class="good-tree"></editor-tree>
              </div>
              <div class="btn-group">
                <div class="btn btn-ok" @click="dialogCancelConfirm"></div>
                <div class="btn btn-cancel" @click="dialogAddCancelFirst"></div>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import SizePrice from "../../components/sizePrice";
import SelectCate from "../../components/selectCate";
import TagEditor from "../../components/tagEditor";
import SUpload from "@/components/upload/SUpload";
import ShowImg from "../../components/showImg";
import EditorTree from "../editorTree";
import StockSelect from "../../components/stockSelect";

// import { Util } from "@/config/util";
import { foodSize, GoodType } from "@/config/cfg";
import { GoodList } from "../../index";

export default {
  props: {
    foodinfo: {
      type: Object
    }
  },
  components: {
    SizePrice,
    SelectCate,
    TagEditor,
    SUpload,
    ShowImg,
    EditorTree,
    StockSelect
  },
  data() {
    return {
      foodNameN: "",
      unitData: [],
      unitSelect: 0,

      categoryArr: [],
      getcategoryType: GoodType.DISH,
      //
      featureObj: {
        name: "",
        value: "shop_feature",
        limitNum: 30,
        limitWord: 5,
        limitSelect: 10
      },
      dialogVisible: false,
      featureData: [],
      //
      sizeData: [
        {
          label: foodSize.toString(foodSize.YES),
          value: foodSize.YES
        },
        {
          label: foodSize.toString(foodSize.NO),
          value: foodSize.NO
        }
      ],
      sizeSelect: foodSize.NO,
      //
      compositionData: [],
      compositionObj: {
        name: "",
        value: "shop_composition",
        limitNum: 30,
        limitWord: 5,
        limitSelect: 10
      },
      compositionDialog: false,
      //
      stockNum: 0,

      imgList: [],
      showPicture: false,
      imgSrc: "",
      base_url: base_url,

      //
      unitObj: {
        name: "",
        value: "food_unit_list",
        limitNum: 30,
        limitWord: 5,
        limitSelect: 1
      },
      unitdialogVisible: false,

      categoryDialogVisible: false,

      isShowCate: true
    };
  },
  watch: {
    foodinfo: {
      handler: function(newVal, oldVal) {
        this.init();
      },
      deep: true
    }
  },
  created() {
    this.init();
  },
  methods: {
    foodNameHandler() {
      this.$emit("foodname-change", this.foodNameN);
    },
    changeUnit(index) {
      this.unitSelect = index;
    },
    //
    showFeatureDialog() {
      this.dialogVisible = true;
    },
    hideDialog() {
      this.dialogVisible = false;
    },
    getFeature(select) {
      this.featureData = select;
      this.$emit("get-feature", select);
    },
    //
    changeSizeSelect(index) {
      this.sizeSelect = index;
    },
    //
    getPrice(price) {
      this.$emit("price-changeT", price);
    },
    //
    imgChangeHandler(select) {
      if (this.imgList.indexOf(select) !== -1) {
        this.$notify({
          title: "",
          message: "",
          type: "warning"
        });
        return;
      } else if (this.imgList.length >= 5) {
        this.$notify({
          title: "",
          message: "5",
          type: "warning"
        });
        return;
      }
      this.imgList.push(select);
      this.$emit("img-changeT", this.imgList);
    },
    //
    showCompositionDialog() {
      this.compositionDialog = true;
    },
    deleteimg(index) {
      this.imgList.splice(index, 1);
    },
    isshowPicture(index) {
      this.imgSrc = this.imgList[index];
      this.showPicture = true;
    },
    isclosePicture() {
      this.showPicture = false;
    },
    hideCompositionDialog() {
      this.compositionDialog = false;
    },
    getComposition(select) {
      this.compositionData = select;
      this.$emit("get-composition", select);
    },
    //
    showUnitDialog() {
      this.unitdialogVisible = true;
    },
    hideUnitDialog() {
      this.unitdialogVisible = false;
    },
    getUnit(select) {
      this.unitData = select;
      this.$emit("foodUnit-change", this.unitData[0]);
    },
    //
    // changeStock() {
    //   this.$emit("stocknum-change", this.stockNum);
    // },
    getStockNum(num) {
      // this.stockNum = num;
      this.$emit("stocknum-change", num);
    },

    //
    initUnit() {
      let unit = this.foodinfo.food_unit;
      let that = this;

      this.unitData.forEach(function(item, index) {
        if (item === unit) {
          that.unitSelect = index;
        }
      });
    },
    getSelectId(id) {
      this.$emit("category-change", id);
    },
    clearcategoryId(id) {
      this.$emit("category-changeT", id);
    },
    init() {
      this.foodNameN = this.foodinfo.food_name;
      this.featureData = this.foodinfo.feature || [];
      this.sizeSelect = this.foodinfo.food_price.type || foodSize.NO;
      this.compositionData = this.foodinfo.composition || [];
      this.stockNum = this.foodinfo.stock_num_day || 0;
      this.imgList = this.foodinfo.food_img_list || [];
      this.categoryArr = this.foodinfo.category || [];

      if (this.foodinfo.food_unit) {
        this.unitData = [];
        this.unitData.push(this.foodinfo.food_unit);
      }

      this.initUnit();
    },

    checkStockNum() {
      this.stockNum = GoodList.checkRound(this.stockNum);
    },

    //
    dialogCancelConfirm() {
      this.categoryDialogVisible = false;
      this.forbidScroll();
      this.rerender();
    },
    dialogAddCancelFirst() {
      this.categoryDialogVisible = false;
      this.forbidScroll();
      this.rerender();
    },
    opencategoryDialog() {
      this.categoryDialogVisible = true;
      this.forbidScroll();
    },
    forbidScroll: function() {
      // ,
      if (this.categoryDialogVisible === true) {
        let $html = document.getElementsByTagName("html")[0];
        let $body = document.getElementsByTagName("body")[0];
        scrollTo(0, 0);
        $html.style.overflow = "hidden";
        $body.style.overflow = "hidden";
      } else if (this.categoryDialogVisible === false) {
        let $html = document.getElementsByTagName("html")[0];
        let $body = document.getElementsByTagName("body")[0];
        $html.style.overflow = "auto";
        $body.style.overflow = "auto";
      }
    },
    rerender() {
      this.isShowCate = false;
      this.$nextTick(function() {
        this.isShowCate = true;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#base-set {
  .content {
    @include fc(14px, #000);
    background-color: #fff;
    padding-bottom: 22px;
  }
}

.food-sn {
  height: 94px;
  line-height: 19px;

  div {
    margin-top: 48px;
  }
}

.food-code {
  .code-content {
    img {
      @include wh(100px, 100px);
    }
  }
}

.food-name {
  height: 34px;
  margin-top: 20px;
  margin-bottom: 20px;

  .name-title {
    line-height: 34px;
  }
}

.food-category {
  height: 34px;
  line-height: 34px;
}

.food-unit {
  height: 30px;
  line-height: 30px;
  margin-top: 22px;
  margin-bottom: 24px;

  .unit-content {
    li {
      @include wh(80px, 30px);
      border-radius: 2px;
      line-height: 30px;
      text-align: center;
      color: #666;
      margin-right: 10px;
      cursor: pointer;
    }

    li.active {
      background-color: $graybg;
    }

    li.editor {
      @include wh(60px, 30px);
      border: 1px solid #4877e7;
      border-radius: 2px;
      color: #4877e7;
      background-color: #fff;
      cursor: pointer;
    }
  }
}

.food-feature {
  height: 30px;
  line-height: 30px;
  margin-bottom: 12px;

  .feature-content {
    li {
      @include wh(80px, 30px);
      background: $graybg;
      border-radius: 2px;
      margin-right: 10px;
      line-height: 30px;
      text-align: center;
    }

    li.editor {
      @include wh(60px, 30px);
      border: 1px solid #4877e7;
      border-radius: 2px;
      color: #4877e7;
      background-color: #fff;
      cursor: pointer;
    }
  }
}

.food-size {
  height: 30px;
  line-height: 30px;
  margin-bottom: 22px;

  .size-content {
    li {
      @include wh(60px, 30px);
      line-height: 30px;
      text-align: center;
      border: 1px solid #d9d9d9;
      color: #333;
      border-radius: 2px;
      margin-right: 10px;
      cursor: pointer;
    }
    li.active {
      border-color: #4877e7;
      color: #fff;
      background-color: #4877e7;
    }
  }
}

.food-price {
  padding-bottom: 20px;

  .price-content {
    .tip {
      font-size: 12px;
      color: #9b9b9b;
    }
  }
}

.food-picture {
  .picture-content {
    .default-img {
      margin-right: 10px;
      float: left;
      @include wh(100px, 100px);
      img {
        @include wh(100px, 100px);
      }
    }

    .picture-group {
      li {
        position: relative;
        border: 1px solid #c0ccda;
        margin-right: 10px;
        @include wh(100px, 100px);
        img {
          @include wh(100%, 100%);
        }

        .picture-actions {
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          cursor: default;
          text-align: center;
          color: #fff;
          opacity: 0;
          font-size: 20px;
          background-color: rgba(0, 0, 0, 0.5);
          transition: opacity 0.3s;

          span {
            line-height: 100px;
          }
          .el-icon-view {
            cursor: pointer;
          }
          .el-icon-delete2 {
            cursor: pointer;
          }
          .picture-delete {
            margin-left: 10px;
          }
        }
      }
      li:hover {
        .picture-actions {
          opacity: 1;
        }
      }
    }
    .sl-imageup {
      height: 100px;

      .img-tip {
        font-size: 12px;
        color: #9b9b9b;
        margin-top: 54px;
        left: 0;
        bottom: 0;
      }
    }
  }
}

.food-composition {
  height: 30px;
  line-height: 30px;
  margin-bottom: 12px;
  margin-top: 22px;

  .composition-content {
    li {
      @include wh(80px, 30px);
      background: $graybg;
      border-radius: 2px;
      margin-right: 10px;
      line-height: 30px;
      text-align: center;
    }

    li.editor {
      @include wh(60px, 30px);
      border: 1px solid #4877e7;
      border-radius: 2px;
      color: #4877e7;
      background-color: #fff;
      cursor: pointer;
    }
  }
}

.food-stock {
  // height: 34px;

  // .stock-title {
  //   line-height: 34px;
  // }
}

.feature-tip {
  @include fc(12px, #9b9b9b);
  margin-left: 234px;
  margin-bottom: 22px;
}

.title {
  @include fc(14px, #666666);
  background-color: #f6f8fc;
  height: 40px;
  line-height: 40px;
  padding-left: 14px;
}

.food-left {
  width: 220px;
  text-align: right;
  opacity: 0.5708;
  margin-right: 14px;
}

.food-right {
  @include fc(14px, #666);
  min-width: 1020px;
  max-width: 1021px;
}

.featureDial {
  @include wh(540px, 493px);
}
span.must {
  position: relative;
}
span.must:before {
  position: absolute;
  left: -10px;
  top: 3px;
  content: "";
  display: block;
  @include wh(10px, 10px);
  background: url(../images/delete.png);
  background-repeat: no-repeat;
  margin-right: 4px;
}
.category-dialog-m {
  .dialog-wrap {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    // background-color: #000;
    // opacity: 0.3;
    z-index: 1200;

    .dialog-cover {
      // 
      position: fixed;
      @include wh(100%, 100%);
      @include tl(0, 0);
      background-color: #000;
      opacity: 0.5;
      z-index: 1201;
    }

    .dialog-content {
      position: absolute;
      left: 50%;
      width: 540px;
      margin-left: -270px;
      top: 8.7%;
      background-color: #fff;
      overflow: auto;
      z-index: 1203;

      .dialog-title {
        height: 40px;
        line-height: 40px;
        background-color: $blue;
        color: #fff;
        text-align: center;
      }
      .category-content {
        padding: 40px 113px 0 113px;
        height: 592px;
      }
      .btn-group {
        font-size: 0;
        margin: 40px 0 50px 80px;
        .btn {
          display: inline-block;
          @include wh(160px,40px);
          color: $blueA;
          border: 1px solid $blueA;
          border-radius: 2px;
          line-height: 40px;
          font-size: 14px;
          text-align: center;
          margin-left: 60px;
          cursor: pointer;
        }
        .btn.btn-ok {
          color: #fff;
          background-color: $blueA;
          margin-left: 0;
        }
      }
    }
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#base-set {
  .el-input {
    // width: inherit;
  }
  .food-name .el-input__inner {
    @include wh(280px, 34px);
    border-radius: 0;
  }
  .el-input__inner:hover {
    border: 1px solid #bfcbd9;
  }
  .el-input__inner:focus {
    border: 1px solid #bfcbd9;
  }
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
  .dialog-add .el-input__inner {
    @include wh(300px, 34px);
    border-radius: 0;
  }
  .food-stock .el-input__inner {
    @include wh(280px, 34px);
    border-radius: 0;
  }
}
</style>



